import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';

type KeyDownType = (event: React.KeyboardEvent, controller: Controller, next: () => void) => void;

export default function createKeyDown(customizedKeyDown?: KeyDownType) {
  return function onKeyDown(
    event: React.KeyboardEvent,
    controller: Controller,
    next: () => void,
  ) {
    return customizedKeyDown ? customizedKeyDown(event, controller, next) : next();
  };
}
